<template>
  <div class="mdl-grid order procurement">
    <div class="mdl-cell--12-col-desktop	mdl-cell--4-col-phone">
      <h4 class="text-center">Invoice</h4>
      <table class="mdl-data-table mdl-js-data-table">
        <thead>
          <tr>
            <th>Product</th>
            <th>Price</th>
            <th>Quantity</th>
            <th>Total Order Quantity</th>
            <th>Amount</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in lineItems" :key="item.product">
            <td>{{item.label}}</td>
            <td>{{item.price}}</td>
            <td>
              <div>
                <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has(item.label)}">
                  <input v-validate="generateValidationRule(item.total_quantity)" :name="item.label" :id="item.label" class="mdl-textfield__input" v-model="item.quantity">
                  <label class="mdl-textfield__label" for="name">QTY</label>
                  <span class="mdl-textfield__error">{{ errors.first(item.label) }}</span>
                </div>
              </div>
            </td>
            <td>{{item.total_quantity}}</td>
            <td>{{ item.price * item.quantity }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="mdl-cell--8-col-desktop	mdl-cell--4-col-phone">
      <transaction-view
        @selectedTransaction="transactionParams=$event"
        @amountEntered ="amount=$event"
        :transactions="params.transactions"
        :client="client"
        ref="transaction"
      ></transaction-view>
    </div>
    <div class="mdl-cell--4-col-desktop	mdl-cell--4-col-phone">
            <div class="md-list">
        <div class="mdl-list__item">
          <div class="mdl-list__item-primary-content">
            Amount
          </div>
          <div class="mdl-list__item-secondary-content">
            {{total}}
          </div>
        </div>
         <div class="mdl-list__item">
          <span class="mdl-list__item-primary-content">
            <div>
              <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('sgst_percentage')}">
                <input v-validate="'required|decimal'" name="sgst_percentage" id="sgst_percentage" class="mdl-textfield__input" v-model="params.sgst_percentage">
                <label class="mdl-textfield__label" for="sgst_percentage">SGST</label>
                <span class="mdl-textfield__error">{{ errors.first('sgst_percentage') }}</span>
              </div>
            </div>
          </span>
          <span class="mdl-list__item-secondary-content">
            {{sgstAmount}}
          </span>
        </div>
        <div class="mdl-list__item">
          <div class="mdl-list__item-primary-content">
            <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('cgst_percentage')}">
              <input v-validate="'required|decimal'" name="cgst_percentage" id="cgst_percentage" class="mdl-textfield__input" v-model="params.cgst_percentage">
              <label class="mdl-textfield__label" for="cgst_percentage">CGST</label>
              <span class="mdl-textfield__error">{{ errors.first('cgst_percentage') }}</span>
            </div>
          </div>
          <div class="mdl-list__item-secondary-content">
            {{cgstAmount}}
          </div>
        </div>
         <div class="mdl-list__item">
          <span class="mdl-list__item-primary-content">
            Pending Amount
          </span>
          <span class="mdl-list__item-secondary-content">
            {{pendingAmount}}
          </span>
        </div>
        <div class="mdl-list__item">
          <span class="mdl-list__item-primary-content">
            Total Amount
          </span>
          <span class="mdl-list__item-secondary-content">
            {{totalAmount}}
          </span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { TransactionView } from 'SHARED/orders/components'
import { mapActions } from 'vuex'
import { reInitiateForm } from '@/helpers.js'

export default {
  name: 'procurement-invoice',
  props: {
    procurement: {
      type: Object,
      required: true
    }
  },
  components: {
    TransactionView
  },
  watch: {
    procurement (value) {
      this.lineItems = value.line_items
      this.client = value.client
      this.params.procurement = value.id
    },
    amount (value) {
      this.calculatePendingAmpount()
    },
    totalAmount () {
      this.calculatePendingAmpount()
    },
    lineItems: {
      deep: true,
      handler: function (val, oldVal) {
        this.calculateTotal()
      }
    }
  },
  updated () {
    window.componentHandler.upgradeAllRegistered()
    reInitiateForm()
  },
  computed: {
    lineItems: {
      get () {
        return this.params.line_items || []
      },
      set (items) {
        items.forEach(item => {
          this.params.line_items.push({
            label: item.label,
            price: item.price,
            quantity: 0,
            product: item.product,
            total_quantity: item.quantity
          })
        })
      }
    },
    client: {
      get () {
        return this.procurement.client || {}
      },
      set (value) {
        this.params.name = value.name
        this.params.gst_number = value.gst_number
        this.params.address = value.address_line_1
      }
    },
    transactionParams: {
      get () {
        return this.params.transactions
      },
      set (data) {
        this.$set(this.params, 'transactions', data)
      }
    },
    sgstAmount () {
      return (this.total * this.params.sgst_percentage) / 100
    },
    cgstAmount () {
      return (this.total * this.params.cgst_percentage) / 100
    },
    totalAmount () {
      return (this.total + this.sgstAmount + this.cgstAmount)
    }
  },
  methods: {
    ...mapActions([
      'createSalesOrder'
    ]),
    calculatePendingAmpount () {
      if (this.params.pending_amount > 0) {
        this.pendingAmount -= this.amount
      } else {
        this.pendingAmount = this.totalAmount - this.amount
      }
    },
    calculateTotal () {
      this.total = 0
      this.lineItems.forEach(item => {
        this.total += (item.price * item.quantity)
      })
    },
    generateValidationRule (maxValue) {
      return `required|integer|min_value:1|max_value:${maxValue}`
    },
    proceed () {
      this.$refs.transaction.valid().then(valid => {
        if (valid) {
          return this.$validator.validateAll()
        }
      }).then(valid => {
        if (valid) {
          this.createSalesOrder(this.params).then(response => {
            this.$emit('orderComplete')
            this.$toastr.s('Success')
          }).catch(e => {
            this.$toastr.e(e)
          })
        }
      })
    }
  },
  data () {
    return {
      params: {
        procurement: '',
        line_items: [],
        sgst_percentage: 0,
        cgst_percentage: 0,
        transactions: [],
        pending_amount: 0,
        name: '',
        address: '',
        gst_number: ''
      },
      amount: 0,
      pendingAmount: 0,
      total: 0
    }
  }
}
</script>



